import React, {Fragment} from 'react';
import styled from 'styled-components';
import {Helmet} from "react-helmet";
import {Link} from "react-router-dom";

const location_titles = {
    "/": "About Team Tana",
    "/callToAction": "Our Appeal",
    "/illustrations": "Illustrations",
    "/models": "3d Models",
    "/pixelart": "Pixel Art",
    "/miniatures": "Painted Miniatures",
    "/commissions": "Commission Catalog"
}

export const HeaderStyled = styled.section`
h1{
    max-width: 100vw;
    a {
        margin-right: 3rem;
    }
    background: linear-gradient(90deg, rgba(242,255,214,.2) 60%, rgba(242,255,214,0) 100%);
    box-shadow: 4px 4px 5px 0px #2e251e80;
    margin: 0;
    font-size: 2rem;
    padding: 0.6rem;
}
`

function back(){
    window.history.back()
}

export default function({location}){
    let loc_title = location_titles[location.pathname]
    return (
        <HeaderStyled>
            <Helmet>
                <title>{loc_title}</title>
            </Helmet>
        <h1>
            {location.pathname !== "/" ? <a onClick={back}>◄</a> : <Fragment/>}
            {loc_title}
        </h1>
        </HeaderStyled>
    )
}