import React, {Fragment} from 'react';
import styled from 'styled-components';
import {
  Link,
  useLocation,
  useParams
} from "react-router-dom";

const ImageCarosel = ({photos}) => {
    let output = []
    for (let index = 0; photos.length > index; index++){
        output.push(<img key={index} src={photos[index].url} alt={`${photos[index].name}`}/>)
    }
    return <Fragment>{output}</Fragment>
}

export const BannerBlock = ({name="", path=name.toLowerCase(), photos=[], className, children}) => 
<BaseBlock className={className}>
<main>
<div className="Description">
<Link to={`/${path}`}><h1 className="BannerHeading">{name}</h1></Link>
{children}
</div>
<div className="Images">
<Link to={`/${path}`}>
<ImageCarosel photos={photos} />
</Link>
</div>
</main>
</BaseBlock>

export const BaseBlock = styled.section`
box-sizing: border-box;
display: flex;
flex-direction: column;
justify-content: center;

p {
    font-weight: bold;
    font-size: 1.133rem
    line-height: 0.9
}

main {
    display: flex;
    margin: 1rem;
    margin-left:2rem;
    min-width: calc(240px + 0.6rem);
    background: linear-gradient(90deg, rgba(242,255,214,.2) 60%, rgba(242,255,214,0) 100%);
    box-shadow: 4px 4px 5px 0px #2e251e80;
    .BannerHeading{
        margin: 0;
        font-size: 1.5rem;
    }
    .Description{
        padding: 0 2.33rem;
        order: 2
    }
    .Images{
        order: 1
        padding: 0.6rem;
    }    
}


`
export default BaseBlock