import React from 'react';
import {BannerBlock} from './BaseBlock';
import {GalleryImg} from './GalleryImage';
import {GalleryPage} from './GalleryPage';

const NAME = "Illustrations"
export const IllustrationCallout = () => 
<BannerBlock className="Callout" path={NAME.toLowerCase()} name={NAME} background="/assets/Background1.jpg" photos={[{name:"Illustrations", url:"/assets/ShadowboxThumb.png"}]}>
    <p>A character in a dynamic pose drawn full body.</p><p>Tiers range from pencil sketch or digital sketch, clean line, simple colors, or detailed colors.</p><p>Backgrounds range from solid color to simple stylized to detailed.</p><p>Additional characters can be added up to three total characters.</p>
</BannerBlock>

export const IllustrationGallery = () => 
<GalleryPage background="/assets/Background1.jpg" title="Illustrations" prints={true}>
    <GalleryImg srcHint="Shadowbox" alt="Hail the King" buyPrice={700} commishPrice={700} printUrl="https://www.redbubble.com/people/aleesatana/works/41050284-hail-the-king">
        <p>A young woman and her winged fox companion discover the remains of a long-dead king.  Painted in gouache as a multi-layer shadowbox.  11x14 inches.</p>
    </GalleryImg>
    <GalleryImg srcHint="Castle" alt="Grimms Fairy Tales" commishPrice={550} printUrl="https://www.redbubble.com/people/aleesatana/works/40667572-grimms-fairy-tales">
        <p>A replacement cover for an aged copy of a beloved book, a dark castle looms over an enchanted landscape.  Painted in gouache.  9x12 inches.</p>
    </GalleryImg>
    <GalleryImg srcHint="Axomaid" alt="Axolotlmaid" commishPrice={100}>
        <p>A cute mermaid based on an albino Axolotl.  Painted in goauche, with a simple watercolor wash background.  5x8.5 inches.</p>
    </GalleryImg>
</GalleryPage>
