import React from 'react';
import {BannerBlock} from './BaseBlock';
import {GalleryImg} from './GalleryImage';
import {GalleryPage} from './GalleryPage';

export const PixelCallout = () => 
<BannerBlock className="Callout" path="pixelart" name="Pixel Art" photos={[{name:"PixelArt", url:"/assets/GrasstilesThumb.png"}]}>
    <p>Hi-bit textures for 2.5d and 2d games</p>
    <p>Hand painted normal, texture and light mapping</p>
    <p>2.5d and 2d shaders</p>
    <p>Expressive actor animations</p>
    <p>Matching close ups of actors</p>
    <p>Tilesets and environments</p>
</BannerBlock>

export const PixelGallery = () => 
<GalleryPage background="/assets/Background1.jpg" title="PixelArt">
    <GalleryImg srcHint="GrassHero" alt="Grassland Hero" commishPrice={1000}>
        <p>An action-adventure hero sprite, including four directions, an idle, four-frame walk and six-frame run.</p>
    </GalleryImg>
    <GalleryImg srcHint="Grasstiles" alt="Grassland Tileset" commishPrice={3000}>
        <p>RPG style game tileset, 32x32px tiles.  Ready for use in Godot, Unity or other game engine.</p>
    </GalleryImg>
    <GalleryImg srcHint="Quakithere" alt="Quakithere" commishPrice={500}>
        <p>An alien creature design and attack animation, for a side-view platformer.</p>
    </GalleryImg>
    <GalleryImg srcHint="Sunhat" alt="Sunhat NPC" commishPrice={40}>
        <p>NPC character for Finding Paraside by Freebird Games.  Created on an existing template.</p>
    </GalleryImg>
</GalleryPage>
