import React from 'react';
import {BannerBlock} from './BaseBlock';
import {GalleryImg} from './GalleryImage';
import {GalleryPage} from './GalleryPage';

const NAME = "Miniatures"
export const MinisCallout = () => 
<BannerBlock className="Callout" path={NAME.toLowerCase()} name={NAME} background="/assets/Background1.jpg" photos={[{name:"Miniatures", url:"/assets/TrollsThumb.png"}]}>
    <p>Painted Miniatures of various brands and styles</p>
    <p>Most for sale and are good examples of how to gauge comission quality concerns</p>
</BannerBlock>

export const MinisGallery = () => 
<GalleryPage title="Miniatures">
    <GalleryImg srcHint="Trolls" alt="Trolls" buyPrice={"15-50"} commishPrice={"15-50"}>
        <p>A collection of my Trollblood Hordes army, painted to high tabletop</p>
        <p>Many more and complete units available as well as solos etc</p>
        <p>Prices range per model</p>
    </GalleryImg>
    <GalleryImg srcHint="Vera" alt="Vera & Worms" buyPrice={40} commishPrice={50}>
        <p>Vera and the Worms</p>
        <p>The inspiration for a capsule adventure line being working on</p>
        <p>Will post complete adventure seed soon</p>
    </GalleryImg>
    <GalleryImg srcHint="MercSolos" alt="Mercenary Solos" buyPrice={30} commishPrice={35}>
        <p>Mercenary Solos, painted to high tabletop standard.</p>
    </GalleryImg>
    <GalleryImg srcHint="PinkMercs" alt="Pink Mercenaries" buyPrice={100} commishPrice={100}>
        <p>Pink Mercenaries</p>
        <p>Part of my Breast Cancer Brawl army, one set of many, but a fantastic starter merc battle group</p>
        <p>Priced as full set, mid to low tabletop standard</p>
        <p>Note: I would love to see these be prize support for Breast Cancer Brawl or similar events.</p>
        <p>I will price them much lower if so.</p>
    </GalleryImg>
    <GalleryImg srcHint="AngryElves" alt="Angry Elves" buyPrice={35} commishPrice={40}>
        <p>Retribution Warcasters from Warmachine, done up in a fall themed color palette.</p>
        <p>I have many other models for this army in various stages of completeness and will be adding more as they are complete</p>
        <p>These are all painted to high tabletop standard, most of the rest of the army is mid-high tabletop or unpainted and ready to order</p>
        <p>Priced per model</p>
    </GalleryImg>
    <GalleryImg srcHint="Archer" alt="Archer" commishPrice={40}>
        <p>Archer I painted up as part of commission recently. Basic tabletop paintjob, quick and fun.</p>
        <p>Includes diorama with lid</p>
    </GalleryImg>
</GalleryPage>
