import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';

const CalloutStyles = styled.section`
position: fixed;
top: 1.4rem;
right: -5rem;
padding: 1rem 5rem;
background: #0d1111cc;
transform: rotate(30deg);
z-index: 10;
h1{
    margin: auto;
}

`

export const CalloutButton = () =>
    <CalloutStyles>     
            <h1 className="CallToAction">
            <Link to="/callToAction">Work Wanted
            </Link></h1>
        
    </CalloutStyles>