import React, {Fragment} from "react";
import {
  TransitionGroup,
  CSSTransition
} from "react-transition-group";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  useLocation,
  useParams
} from "react-router-dom";

import {IllustrationCallout, IllustrationGallery} from './Illustration'
import {ModelsCallout,ModelsGallery} from './Models';
import {Transitions} from './Transitions';
import { CallToAction, ActionCallout } from "./CallToAction";
import {CalloutButton} from './CallOutButton';
import { PixelGallery, PixelCallout } from "./PixelArt";
import {MinisCallout, MinisGallery} from "./Miniatures";
import ComishHeader from './Header';
import ScrollToTop from "./scrollToTop";
import {Landing} from './Landing';

export default function AnimationRouteWrapper() {
  return (
    <Transitions>
      <Router>
        <Switch>
          <Route path="*">
            <AnimatedTransitions />
          </Route>
        </Switch>
      </Router>
    </Transitions>
  );
}

function AnimatedTransitions() {
  let location = useLocation();
  return (
    <Fragment>
    <ScrollToTop />
    <ComishHeader location={location} />
    {location.pathname !== "/callToAction" ? <CalloutButton/> : <Fragment/>}
    <TransitionGroup>
    <CSSTransition
      key={location.key}
      classNames="zoom-in"
      timeout={900}
    >
      <Switch location={location}>
          <Route exact path="/illustrations">
            <IllustrationGallery />
          </Route>
          <Route exact path="/callToAction">
            <CallToAction />
          </Route>
          <Route exact path="/models">
            <ModelsGallery />
          </Route>
          <Route exact path="/pixelart">
            <PixelGallery/>
          </Route>
          <Route exact path="/miniatures">
            <MinisGallery/>
          </Route>
          <Route exact path="/commissions">
          <div>
            <IllustrationCallout />
            <ModelsCallout />
            <PixelCallout />
            <MinisCallout />
            <ActionCallout />
          </div>
          </Route>
          <Route path="/">
            <Landing />
            <IllustrationCallout />
            <ModelsCallout />
            <PixelCallout />
            <MinisCallout />
            <ActionCallout />
          </Route>
      </Switch>
    </CSSTransition>
  </TransitionGroup>
  </Fragment>
  );
}