import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {BannerBlock} from './BaseBlock';

const LandingStyles = styled.section`
margin: auto;
margin-top: 2rem;
background: linear-gradient(90deg, #f2ffd620 60%, #f2ffd600 100%);
min-height: 100vh;

header{
    max-width: 80vw;
    max-height:480px;
    margin: 0 auto;
    padding-top: 1rem;
    display: flex;
    justify-content: space-between;
    img{
        max-height: 240px;
        max-width: 240px;
    }

    div {
        h1,h2,h3 {
            text-align: center;
            margin: 0.1rem;
            padding: 0;
        }
        h1 {
            font-size: 3rem;
        }
        h2 {
            font-size: 2.33rem;
        }
        h3 {
            font-size: 1.66rem;
        }
        max-width: 20rem;
    }
}

main{
    margin: auto;
    padding: 1rem;
}

footer{
    margin: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    h3 {
        font-size: 1.1rem;
    }
}

.CallBack{
    flex: 1 0 100%;
    text-align: center;
}

a.External{
    flex: 1 0 40%;
    margin:20px;
    display: flex;
}


`

export const Landing = (props) =>
    <LandingStyles>
        <header className="landingHeader">
            <img src="/assets/ShadowboxThumb.png"/>
            <div>
                <h2>Who We Are?</h2> 
                <h1>Team Tana</h1>
                <h3>aka Aleesa and Liz Tana</h3>
            </div>
            <img src="/assets/TrollsThumb.png"/>
        </header>
        <main>
        <h2>What We Do?</h2>
        <p>Make games and art and games as art and art as games</p>
        <h2>Why Am I Here?</h2>
        <p>To see what we make and look into promoting, buying or contributing in some way to the art and games</p>
        <h2>Where?</h2>
        <p>In a very expensive neighborhood trying to find work in the games industry, currently trying to find freelance work to keep our lease going, which we are two months behind on, and have a 14, well, now 13 day notice of eviction from?.</p>
        <h2>How can I help?</h2>
        <p>Commissioning new works or buying existing works in our Commission Catalog, Patronage vie Patreon, or in general getting in touch with short turn around freelance work.</p>
        {props.children}
        </main>
        <footer>
        <a className="External" href="https://discord.gg/kSQp9p3"><img src="/assets/Discord-Logo-White.png" alt="Discord" width="80px" height="80px"/>
        <h3>Join us on Discord, we appreciate the company and it's a great place to discuss commissions, freelancing, or just about anything!</h3></a>
        <a className="External" href="https://www.patreon.com/Aleesatana"><img src="/assets/Patreon_Mark_White.png" alt="Patreon" width="80px" height="80px"/>
        <h3>To support our ongoing game publishing efforts, come see our Patreon</h3></a>
        <Link className="CallBack" to="/callToAction"><h3>To see a video appeal on why we find games and art so important, please see our Appeal</h3></Link>
        </footer>
    </LandingStyles>