import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {BannerBlock} from './BaseBlock';

export const ActionCallout = () => 
<BannerBlock className="Callout" name="Our Appeal" path="callToAction" photos={[{name:"Our Appeal", url:"/assets/CastleThumb.png"}]}>
    <p>All items presented are examples of work you might commission directly.</p>
    <p>Some items are also currently marked for sale.</p>
    <p>Currently behind on rent and looking for help.</p>
    <p>Offering commissions and am available for freelance and contract work.</p>
    <p>Connect with us on Discord to talk about how you can help.</p>
</BannerBlock>

const ActionStyles = styled.section`
margin: auto;
margin-top: 2rem;
background: linear-gradient(90deg, rgba(242,255,214,.2) 60%, rgba(242,255,214,0) 100%);
min-height: 100vh;

header{
    max-width: 854px;
    margin: 0 auto;
    padding-top: 1rem;
    iframe{
        max-width: 854px;
        max-height:480px;    
        margin: auto;
        box-shadow: 4px 4px 5px 0px #2e251e80;
    }  
}

main{
    margin: auto;
    padding: 1rem;
}

footer{
    margin: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    h3 {
        font-size: 1.1rem;
    }
}

.CallBack{
    flex: 1 0 100%;
    text-align: center;
}

a.External{
    flex: 1 0 40%;
    margin:20px;
    display: flex;
}


`

export const CallToAction = () =>
    <ActionStyles>
        <header className="callToActionHeader">
        <h2>Team Tana is scrambling for rent and needs your help finding work quick!</h2>
        <iframe frameBorder="0" scrolling="no" marginHeight="0" marginWidth="0" width="854" height="480" src="https://www.youtube.com/embed/UAuuOc1r-1g"></iframe>
        </header>
        <main>
        <h2>Aleesa Pixel Tana</h2>
        <p>I’m a pencil-and-paper animator that loves pixel art, low-poly 3D models, that also knows how to write UI and shader code, while contributing meaningfully to discussions of game design and user experience.  I enjoy creating character artwork that evokes a living story and utilizes a variety of cartoony styles.  I am greatly inspired by manga, comic book and animation artists including Bryan Konietzko, Wendy Pini, CLAMP, Gurihiru, Bruce Timm, Naoko Takeuchi and many more.I love working in colerase pencils, alcohol markers, gouache, pixel art and hand painted 3D.  I enjoy taking my art beyond still flat pictures by introducing movement, either through animation, interactivity, three-dimensionality and the play of light. I want to make art that inspires others to be creative, and encourages others that, like me, have suffered trauma, to believe that healing is possible through creativity.
        </p>
        <h2>Liz H Tana</h2>
        <p>I’m web and game developer with a fair bit of QA experience. I've always loved RPGs, especially story driven action RPGs, and I've been working hard to make my own for a long time now. Got a lot of anxiety that keeps me from delivering on things, and working on this project to get through that.</p>
        </main>
        <footer>
        <Link className="CallBack" to="/commissions"><h1>►Visit at our Commissions Gallery to get a good look at what we do◄</h1></Link>
        <a className="External" href="https://www.patreon.com/Aleesatana"><img src="/assets/Patreon_Mark_White.png" alt="Patreon" width="80px" height="80px"/>
        <h3>To support our ongoing publishing efforts, come see our Patreon</h3></a>
        <a className="External" href="https://discord.gg/kSQp9p3"><img src="/assets/Discord-Logo-White.png" alt="Discord" width="80px" height="80px"/>
        <h3>Join us on Discord, we appreciate the company and it's a great place to discuss commissions, freelancing, or just about anything!</h3>
        </a>
        </footer>
    </ActionStyles>