import React, {Fragment} from 'react';
import styled from 'styled-components';

export const Transitions = styled.section`
.zoom-in-enter{
    opacity: 0.3;
    transform:scale(0.33) translateY(-30%);
    z-index: 2;
}
.zoom-in-enter-active, zoom-in-enter-done{
    transition: all 400ms;
    transform: scale(1) translateY(0%);
    z-index: 2;
    opacity: 1;
}
.zoom-in-exit{
    opacity: 1;
    transform: scale(1) translateY(0%);;
    z-index: 3;
}
.zoom-in-exit-active{
    transition: all 400ms;
    opacity: 0.1;
    transform: scale(0.33) translateY(-30%);;
    z-index: 2;
}
`