import React, {useState, Fragment} from 'react';
import {BannerBlock} from './BaseBlock';
import {GalleryItem} from './GalleryImage';
import {GalleryPage} from './GalleryPage';


export const ModelsCallout = () => 
<BannerBlock className="Callout" name="Models" background="/assets/Background1.jpg" photos={[{name:"3DModels", url:"/assets/PrincessThumb.png"}]}>
    <p>3d Models optimized for mobile</p>
    <p>Characters, Creatures, Vehicles and Environments</p>
    <p>Can include rig and animations</p>
    <p>Materials setup, including PBR, node-based and code based shaders, and hand painted texturing</p>
</BannerBlock>

export const GalleryModel = (props) => {
    var [hide,setHide] = useState(true);
    const handleHideClick = (event) => {
        setHide(!hide);
    }
    var combineProps = {...props,hide,toggleHide:handleHideClick}
    return <InnerGalleryModel {...combineProps} />
}

const ExternalModel = ({external,hide}) => {
    if(hide){
        return <div></div>
    }else{
           return (<div class="sketchfab-embed-wrapper">
            <iframe title="A 3D model" width="640" height="480" 
            src={`${external}/embed?autostart=1&amp;preload=1`}
            frameborder="0" allow="autoplay; fullscreen; vr" mozallowfullscreen="true" webkitallowfullscreen="true"></iframe>
            </div>);
    }
}

const InnerGalleryModel = ({external,srcHint,alt,buyPrice,copyPrice,commishPrice,children,hide,toggleHide}) =>
    <GalleryItem key={srcHint}>
        <h2>{alt}</h2>
        <div className='ImageBox'>
            <img onClick={toggleHide}  src={`/assets/${srcHint}Thumb.png`} alt={alt} />
        </div>
        <div className="RibbonContainer">
        {buyPrice ? 
            <h3 className=".Buy"><span aria-label="Buy the original" role="image">💎</span> ${buyPrice}</h3>
        : <Fragment />}
        {copyPrice ? 
            <h3 className=".Copy"><span aria-label="Buy as Download" role="image">📥</span> ${copyPrice}</h3>
        : <Fragment />}
        {commishPrice ? 
            <h3 className=".Commish"><span aria-label="Commission new project like this" role="image">🎨</span> ${commishPrice}</h3>
        : <Fragment />}
        </div>
        <div className="Description">
            {children}
        </div>
        <div onClick={toggleHide} 
        className={`Modal 
        ${hide? "Modal-exit Modal-exit-active":"Modal-enter Modal-enter-active" }`}>
            <ExternalModel external={external} hide={hide} />
        </div>
        
    </GalleryItem>

export const ModelsGallery = () => 
<GalleryPage background="/assets/Background1.jpg" downloads={true} title="3DModels">
    <GalleryModel srcHint="Mermaid" alt="Mermaid" commishPrice={180} external="https://sketchfab.com/models/e11ffeb575554b34a977152062ebe0ef">
        <p>Stylized mermaid character created for Mermaid Dash, a 3d mobile flappy bird clone, 338 tris, hand-painted texture, and simple animations</p>
    </GalleryModel>
    <GalleryModel srcHint="Princess" alt="Royal Teen" commishPrice={360} external="https://sketchfab.com/models/537b79b6752c4b64a7a434cbae3ffb9c">
        <p>Royal teenage character created for a action puzzle game, 935 tris, hand-painted texture.  One of three alternate outfits.</p>
    </GalleryModel>
    <GalleryModel srcHint="Vulcan" alt="Vulcan Centaur" commishPrice={2400} external="https://sketchfab.com/models/41970ea193144cbc8f2d53eb57e05f92">
        <p>Rocket model created for ULA Anywhere, an Augmented Reality mobile game. 21.6k tris, hand-painted PBR textures.  One of three rockets included in the app.</p>
    </GalleryModel>
</GalleryPage>
