import React, {Fragment} from 'react';
import styled from 'styled-components';

const GalleryStyles = styled.section`
width: 100%;
min-height: 100vh;
margin: 0;
margin-top: 2rem;
overflow-y: scroll;

.GalleryImages {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
}

.GalleryKey {
    font-size: 0.8rem;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    background: linear-gradient(90deg, rgba(242,255,214,.2) 60%, rgba(242,255,214,0) 100%);
    margin-bottom: 0.5rem;
    h3, h4 {
        padding: 0.1rem;
        margin: 0.1rem;
    }
}

`

/*
    Prints: 🖨
  Downloads: 📥
  Commission: 🎨
  Original for Sale: 💎
*/

export const GalleryPage = ({children, downloads, prints}) =>
    <GalleryStyles>
        <header className="GalleryKey">
            <h3>Pricing Key</h3>
            <h4>Commission Example: 🎨</h4>
            <h4>Original for Sale: 💎</h4>
            { downloads ? <h4>Downloads for Sale: 📥</h4> : <Fragment/>}
            { prints ? <h4>Prints for Sale: 🖨️</h4>: <Fragment/>}
        </header>
        <main className="GalleryImages">
            {children}
        </main>
    </GalleryStyles>